module.exports = "\r\n/**\r\n * 先给所有元素加上过渡效果\r\n */\r\n\r\n#container,#container pre,#container div {\r\n  transition: all 1s;\r\n}\r\n\r\n\r\n/**\r\n * 这好像没有什么变化\r\n *\r\n * 白底黑字有点无聊啊, 让我们加点样式\r\n */\r\n\r\n#container {\r\n  background: #0c0f14;\r\n}\r\n\r\npre, a {\r\n  color: white;\r\n}\r\n\r\n\r\n/**\r\n * 这样好一点了\r\n *\r\n * 但是这个编辑区有点宽了\r\n *\r\n * 我要让它看起来舒服些\r\n */\r\n\r\npre {\r\n  width:'$preWidth';\r\n  background: #1b2123;\r\n  font-size: 15px;\r\n  transform: '$preTrans';\r\n  font-family: monospace;\r\n  border-radius: 6px;\r\n}\r\npre:not(:empty) {\r\n  padding: 10px 20px 20px;\r\n}\r\n\r\n/**\r\n * 这样好点了. 但是所有代码都是白色的啊~\r\n * 让我们把它们更好阅读吧\r\n */\r\n\r\n.comment       { color: #6a9955; font-style: italic; }\r\n.selector      { color: #d7ba7d; }\r\n.selector .key { color: #9cdcfe; }\r\n.key           { color: #9cdcfe; }\r\n.value         { color: #ce9178; }\r\n\r\n\r\n/**\r\n * 现在应该比较适合观看了, 我们要准备加入一些内容了\r\n * 让我们先做一些准备工作\r\n */\r\n \r\n#style-text {\r\n  max-height: '$firstStagePreMaxH';\r\n  transform: translateX(0);\r\n  width: '$styleTxtW';\r\n}\r\n\r\n\r\n/**\r\n * 这样就足够了吗？还不够，我们继续看吧！\r\n */\r\n\r\n#work-text {\r\n  '$workTxtPos'\r\n  background: rgb(255,255,255);\r\n  color: #333;\r\n  max-height: '$firstStagePreMaxH';\r\n}\r\n\r\n"